/* *** Navbar *** */
nav {
  position: relative;
  width: 100%;
  height: 20em;
  max-width: 1024px;
  margin: auto; }
  nav .avatar {
    position: absolute;
    left: 30px;
    bottom: 30px;
    border-radius: 16px;
    box-shadow: 0 5px 18px rgba(0, 0, 0, 0.6);
    overflow: hidden;
    width: 12em;
    height: 16em;
    cursor: pointer;
    transition: 0.5s; }
    nav .avatar .avatar__image {
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      background-color: black;
      z-index: 2;
      transition: 0.5s; }
      nav .avatar .avatar__image img {
        width: 100%;
        height: 100%; }
    nav .avatar .avatar__icons {
      position: absolute;
      top: 55%;
      left: 50%;
      display: flex;
      transform: translate(-50%, -50%);
      z-index: 3; }
      nav .avatar .avatar__icons li {
        list-style: none; }
      nav .avatar .avatar__icons a {
        opacity: 0;
        transition: 0.4s;
        transform: translateY(200px); }
        nav .avatar .avatar__icons a i {
          color: black;
          transition: 0.8s; }
        nav .avatar .avatar__icons a:hover {
          background: #000;
          transition: 0.2s; }
          nav .avatar .avatar__icons a:hover i {
            transform: rotateY(360deg);
            color: white; }
    nav .avatar .avatar__details {
      position: absolute;
      bottom: -120px;
      left: 0;
      border-radius: 10px 10px 0 0;
      opacity: 0;
      width: 100%;
      height: 5em;
      padding: 8px 0 3px 0;
      background: #ffffff;
      z-index: 3;
      transition: 0.4s; }
    nav .avatar:hover .avatar__image {
      transform: rotateY(180deg);
      transition: all 0.9s; }
      nav .avatar:hover .avatar__image img {
        opacity: 0.4;
        transition: 0.5s; }
    nav .avatar:hover .avatar__icons li a {
      opacity: 1;
      transform: translateY(0px); }
    nav .avatar:hover .avatar__icons li:nth-child(1) a {
      transition-delay: 0.1s; }
    nav .avatar:hover .avatar__icons li:nth-child(2) a {
      transition-delay: 0.2s; }
    nav .avatar:hover .avatar__icons li:nth-child(3) a {
      transition-delay: 0.3s; }
    nav .avatar:hover .avatar__icons li:nth-child(4) a {
      transition-delay: 0.4s; }
    nav .avatar:hover .avatar__details {
      bottom: 0;
      left: 0;
      transition-delay: 0.5s;
      opacity: 1; }
  nav .tab-selector {
    position: absolute;
    right: 0;
    bottom: 0;
    display: inline-flex;
    border-radius: 1em;
    background-color: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px); }
    nav .tab-selector .tab-selector__child {
      position: relative;
      margin: 0 0.5rem;
      padding: 0.5rem 2rem;
      text-decoration: none;
      text-transform: uppercase;
      color: white; }
      nav .tab-selector .tab-selector__child::before {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        content: " ";
        transition: 0.2s;
        border-radius: 25px; }
      nav .tab-selector .tab-selector__child:hover {
        color: black; }
        nav .tab-selector .tab-selector__child:hover::before {
          transform: scale(1.2);
          box-shadow: 0px 3px 20px 0px black;
          background: linear-gradient(to bottom, #e8edec, #d2d1d3); }
    nav .tab-selector .tab-selector__child--active {
      text-shadow: 0 0 10px white, 0 0 20px white, 0 0 40px white, 0 0 80px white, 0 0 160px white; }
  nav .menu {
    position: absolute;
    top: 10px;
    left: 10px;
    display: none;
    font-size: 1.5em;
    z-index: 3; }

@media screen and (max-width: 768px) {
  nav {
    height: 15em; }
    nav .avatar {
      left: 0;
      right: 0;
      top: 0;
      width: 13em;
      height: 13em;
      margin: 1em auto;
      border-radius: 50%; }
      nav .avatar .avatar__icons {
        display: none; }
      nav .avatar .avatar__details {
        border-radius: 0; }
    nav .tab-selector {
      position: absolute;
